*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  min-width: 320px;
}

body {
  height: 100vh;
  min-height: 1000px;
  min-width: 320px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #110A1D;
  font-family: "Lexend", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-style: normal;
  font-weight: normal;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  position: relative;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: -1;
}
.background::before {
  content: "";
  position: absolute;
  width: 500px;
  height: 1302px;
  top: 50%;
  left: 20%;
  background-image: radial-gradient(ellipse at center, #478BF9, transparent 70%);
  background-size: contain;
  background-repeat: no-repeat;
  opacity: 0.3;
  transform: translateY(-50%) translateX(-50%) rotate(-45deg);
}
.background::after {
  content: "";
  position: absolute;
  width: 800px;
  height: 800px;
  right: 10%;
  bottom: 15%;
  background-image: radial-gradient(ellipse at center, #478BF9, transparent 70%);
  background-size: contain;
  background-repeat: no-repeat;
  opacity: 0.3;
  transform: translateY(50%) translateX(50%);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 0;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  border: 1px solid transparent;
  vertical-align: middle;
  cursor: pointer;
}

@media (max-width: 1399px) {
  body {
    height: auto;
    min-height: 100vh;
  }
  .background::before {
    left: 50%;
    top: 350px;
  }
}
@media (max-width: 999px) {
  .background::before {
    width: 460px;
    height: 1100px;
  }
}
@media (max-width: 639px) {
  .background::before {
    width: 200px;
    height: 700px;
    top: 230px;
    left: 30%;
  }
  .background::after {
    width: 600px;
    height: 600px;
    bottom: 20%;
  }
}
.btn--blue {
  background: #478BF9;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  outline: none;
  cursor: pointer;
}

.check-menu {
  display: none;
}

.header {
  width: 100%;
  background-color: #110A1D;
}

.header__wrapper {
  position: relative;
  width: 100%;
  max-width: 1640px;
  margin: 15px auto 8px;
  padding: 20px 48px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: 4px solid #478BF9;
  border-radius: 45px;
}

.header__logo {
  width: 287px;
  height: 32px;
}

.header__burger-toggle {
  display: none;
}

.header__menu-wrapper {
  width: 75%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header__language-button {
  position: absolute;
  right: 245px;
  margin-right: 12px;
  padding: 12px 14px 12px 38px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #FFFFFF;
  border: 2px solid #9DADF2;
  border-radius: 20px;
  background-color: transparent;
  background-image: url("./../assets/icons/icon-planet.svg");
  background-size: 16px 16px;
  background-position: left 10px center;
  background-repeat: no-repeat;
  z-index: 1000;
  cursor: pointer;
}

#language {
  display: none;
}

.header__language-button::before {
  content: "EN";
}

#language:checked ~ .header__language-button::before {
  content: "RU";
}

@media (max-width: 1399px) {
  .header__wrapper {
    width: 96%;
    padding: 15px 10px;
  }
  .header__logo {
    width: 185px;
    height: auto;
  }
  .header__menu-wrapper {
    width: 80%;
  }
  .header__language-button {
    margin-right: 0;
    right: 196px;
    font-size: 14px;
    line-height: 16px;
  }
}
@media (max-width: 999px) {
  .header {
    position: fixed;
    z-index: 1000;
  }
  .header__wrapper {
    padding: 5px 10px;
    margin: 10px auto;
    border: 2px solid #478BF9;
  }
  .header__logo {
    width: 85px;
    height: auto;
  }
  .header__logo img {
    width: 85px;
    height: auto;
  }
  .header__burger-toggle {
    display: block;
    width: 28px;
    height: 28px;
    padding: 0;
    position: absolute;
    top: 10px;
    right: 26px;
    outline: none;
    background-color: transparent;
    background-image: url("./../assets/icons/burger-open.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 21px 14px;
    cursor: pointer;
    z-index: 3000;
  }
  #burger:checked ~ .header .header__burger-toggle {
    background-image: url("./../assets/icons/burger-close.svg");
    background-size: 16px 16px;
  }
  #burger:checked ~ .header .header__menu-wrapper {
    position: fixed;
    top: 68px;
    right: 0;
    transform: translateX(0%);
    transition: all 0.7s ease-out;
  }
  .header__menu-wrapper {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: fixed;
    top: 68px;
    right: -500px;
    width: 100%;
    height: 560px;
    margin: 0px;
    padding-left: 0;
    padding-top: 12px;
    background-color: #110A1D;
    transform: translateX(100%);
    transition: all 0.7s ease-out;
    z-index: 1000;
  }
  .header__language-button {
    position: relative;
    padding: 16px;
    right: 54px;
    font-size: 0;
    line-height: 0;
    background-size: 20px 20px;
    background-position: center;
    border-radius: 50%;
  }
}
@media (max-width: 999px) and (max-width: 639px) {
  .header__wrapper {
    padding: 10px 16px 8px;
  }
  .header__logo {
    width: 54px;
  }
  .header__burger-toggle {
    top: 12px;
    right: 19px;
  }
  .header__language-button {
    right: 40px;
    top: -2px;
  }
}
.overlay {
  display: none;
}

@media (max-width: 999px) {
  #burger:checked ~ .overlay {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
  }
}
.main-nav {
  position: relative;
  padding-left: 62px;
}

.main-nav__wrapper {
  padding-left: 37px;
}

.main-nav__list {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding-left: 0;
}

.main-nav__item {
  list-style: none;
  margin-right: 30px;
}

.main-nav__link {
  display: inline-block;
  position: relative;
  color: #FFFFFF;
  cursor: pointer;
}
.main-nav__link:hover {
  color: #478BF9;
  transition: 200ms;
}
.main-nav__link:before {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #478BF9;
  transform: scaleX(0);
  transition: all 0.5s;
}
.main-nav__link:hover:before {
  transform: scaleX(1);
}
.main-nav__link:active {
  opacity: 0.5;
}

@media (max-width: 1399px) {
  .main-nav {
    padding-left: 25px;
  }
  .main-nav__wrapper {
    padding-left: 0;
  }
  .main-nav__item {
    margin-right: 14px;
  }
  .main-nav__link {
    font-size: 14px;
    line-height: 16px;
  }
}
@media (max-width: 999px) {
  .main-nav {
    width: 100%;
    padding-left: 0;
  }
  .main-nav__wrapper {
    width: 100%;
  }
  .main-nav__list {
    width: 100%;
    flex-direction: column;
    justify-content: flex-start;
  }
  .main-nav__item {
    width: 95%;
    margin: 0 auto;
    margin-bottom: 5px;
    text-align: left;
    padding-left: 20px;
    padding-top: 12px;
    padding-bottom: 10px;
    border: 2px solid #478BF9;
    border-radius: 50px;
  }
  .main-nav__link {
    display: block;
    color: #FFFFFF;
    padding-bottom: 0;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__link:before {
    display: none;
  }
}
.widgets {
  width: 300px;
  padding-left: 100px;
  display: flex;
  justify-content: flex-end;
