.chips__wrapper {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  overflow: hidden;
}

.chips__line {
  margin: 0 5px;
  overflow: hidden;
  overflow-y: auto;
}

.chips__line--one {
  opacity: 20%;
  align-self: flex-end;
  animation: chips-move-down linear infinite 3s;
}

.chips__line--two {
  opacity: 60%;
  animation: chips-move-up linear infinite 3s;
}

.chips__line--three {
  opacity: 40%;
  align-self: flex-end;
  animation: chips-move-down linear infinite 3s;
}

@keyframes chips-move-down {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(25%);
  }
}
@keyframes chips-move-up {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-25%);
  }
}
@media (max-width: 1399px) {
  .chips__wrapper {
    height: auto;
    padding-top: 60px;
    flex-direction: column-reverse;
  }
  .chips__line {
    display: flex;
    flex-direction: row;
  }
  @keyframes chips-move-down {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(25%);
    }
  }
  @keyframes chips-move-up {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-25%);
    }
  }
}
@media (max-width: 639px) {
  .chips__wrapper {
    padding-top: 26px;
  }
  .chips__line {
    margin: 0 10px;
  }
}
.chip {
  width: 99px;
  height: 99px;
  margin: 15px 5px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.chip--long {
  height: 220px;
  border: 3px solid #478BF9;
  border-radius: 50px;
}

.chip--bottom {
  background-position: bottom -3px center;
}

.chip--top {
