.footer {
  border: 4px solid #478BF9;
  border-bottom: none;
  border-radius: 80px 80px 0 0;
  background-color: #1E1231;
}

.footer__wrapper {
  width: 90%;
  max-width: 1640px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

@media (max-width: 1399px) {
  .footer__wrapper {
    width: 90%;
    padding-top: 20px;
  }
}
@media (max-width: 639px) {
  .footer {
    border: 2px solid #478BF9;
    border-bottom: none;
    border-radius: 50px 50px 0 0;
  }
  .footer__wrapper {
    padding-top: 2px;
    flex-direction: column;
    align-items: flex-start;
  }
}
.footer-nav {
  padding-left: 0;
  display: flex;
}

.footer-nav__item {
  list-style: none;
}

.footer-nav__item:not(:last-child) {
  margin-right: 41px;
}

.footer-nav__link {
  display: inline-block;
  position: relative;
  color: #FFFFFF;
  cursor: pointer;
}
.footer-nav__link:hover {
  color: #478BF9;
  transition: 200ms;
}
.footer-nav__link:before {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #478BF9;
  transform: scaleX(0);
  transition: all 0.5s;
}
.footer-nav__link:hover:before {
  transform: scaleX(1);
}
.footer-nav__link:active {
  opacity: 0.5;
}

@media (max-width: 1399px) {
  .footer-nav__item:not(:last-child) {
    margin-right: 30px;
  }
}
@media (max-width: 999px) {
  .footer-nav {
    flex-direction: column;
  }
  .footer-nav__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 15px;
  }
}
@media (max-width: 639px) {
  .footer-nav__item:not(:last-child) {
    margin-bottom: 1px;
  }
  .footer-nav__link {
    font-size: 14px;
    line-height: 20px;
  }
}
