@font-face {
  font-family: 'Lexend';
  src: local('Lexend Regular'), local('Lexend-Regular'),
      url('../assets/fonts/Lexend-Regular.woff2') format('woff2'),
      url('../assets/fonts/Lexend-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lexend';
  src: local('Lexend Medium'), local('Lexend-Medium'),
      url('../assets/fonts/Lexend-Medium.woff2') format('woff2'),
      url('../assets/fonts/Lexend-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lexend';
  src: local('Lexend SemiBold'), local('Lexend-SemiBold'),
      url('../assets/fonts/Lexend-SemiBold.woff2') format('woff2'),
      url('../assets/fonts/Lexend-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Futura PT';
  src: local('Futura PT Medium'), local('FuturaPT-Medium'),
      url('FuturaPT-Medium.woff2') format('woff2'),
      url('FuturaPT-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}